import React from "react";

import { fireEvent, render, screen } from "@testing-library/react";

import MenuBarIconButton from "../MenuBarIconButton";

describe("The MenuBarIconButton component", () => {
	test("should render properly with icon", () => {
		const handleClick = jest.fn(() => { });
		render(
			<MenuBarIconButton
				dataQA="menu-bar-icon-button"
				onClick={handleClick}
				icon="Bell"
				ariaLabel="notifications"
			/>,
		);

		expect(screen.getByTestId("menu-bar-icon-button-icon")).toBeInTheDocument();
	});

	test("should fire an event when clicked.", () => {
		const handleClick = jest.fn(() => { });
		render(
			<MenuBarIconButton
				dataQA="menu-bar-icon-button"
				onClick={handleClick}
				icon="Bell"
				ariaLabel="notifications"
			/>,
		);
		fireEvent.click(screen.getByTestId("menu-bar-icon-button"));
		expect(handleClick).toBeCalled();
	});
});
